import React, { useState } from "react";
import add from "../../img/logos/add.svg";
import "../../styles/addCardButton/AddPokebolaButton.css";

// Es el botn circular con el signo ms dentro

// Define el componente funcional AddPokebolaButton que recibe una funcin llamada onAddPokebolaClick como prop
const AddPokebolaButton = ({ onAddPokebolaClick }) => {
  // Estado para controlar la visibilidad del botn, comienza como visible (true)
  const [isVisible, setIsVisible] = useState(true);

  // Funcin que maneja el clic en el botn
  const handleButtonClick = () => {
    // Cambia el estado de visibilidad a false cuando se hace clic en el botn
    setIsVisible(false);

    // Llama a la funcin proporcionada como prop (onAddPokebolaClick)
    onAddPokebolaClick();
  };

  // Renderiza el contenido del componente
  return (
    <div>
      {/* Botn con una imagen (add) que ejecuta la funcin handleButtonClick cuando se hace clic */}
      <button
        type="button"
        className={`add-pokebola-button ${isVisible ? "" : "hidden"}` } // Agrega o quita la clase "hidden" dependiendo del estado de visibilidad
        onClick={handleButtonClick}
      >
        {/* Imagen del smbolo de suma (add) */}
        <img src={add} alt="Add Circle" />
      </button>
    </div>
  );
};

// Exporta el componente AddPokebolaButton para que pueda ser utilizado en otros archivos
export default AddPokebolaButton;

